import React, {Component} from 'react'
import Aux from '../../../hoc/Aux/Aux'
import Button from '../../UI/Button/Button'

class OrderSummary extends Component {

  // componentDidUpdate () {
  //   console.log('[OrderSummary] got Update');
  // }


  render () {
    const ingredientSummary = Object.keys( this.props.ingredients )
    .map((igKey,i) => {
      return (
        <li key={igKey+1000*i}>
          <span style={{textTransform: 'capitalize'}}>{igKey}</span>
          :{this.props.ingredients[igKey]}
        </li>
      )
    })

    return (
    <Aux>
      <h3>Your Order</h3>
      <p>A delicious burger with the following ingredients</p>
      <ul>
        {ingredientSummary}
      </ul>
      <p>Your total is : <strong>{this.props.price} $</strong> only !!!</p>
      <p>Continue to Checkout?</p>
      <Button btnType='Danger' clicked={this.props.cancelClick}>CANCEL</Button>
      <Button btnType='Success' clicked={this.props.successClick}>CONTINUE</Button>
    </Aux>
    )
  }
}

export default OrderSummary;


